import React from 'react';
import Telefonos from './telefonos';
import Redes from './redes';

const Contactos = () =>{
    //Crear estado para el consumo de la API
    const numeros = [
        {idNumero: 1, numero: "7930-0980", clase: "fas fa-phone-alt"},
        {idNumero:2, numero: "7588-3451", clase: "fas fa-phone-alt"}
    ];

    const enlacesRedes = [
        {idEnlace: 1, enlace: "https://es-la.facebook.com/CASAJUVENILDPAZ/", clase: "fab fa-facebook-square"},
        {idEnlace: 2, enlace: "mailto:juvenildepazes@gmail.com", clase: "fas fa-envelope"}
    ];

    return (
        <div className="contactos">
            <Telefonos numeros={numeros}/>
            <Redes enlaces={enlacesRedes}/>
        </div>
    );
};

export default Contactos;