import React from 'react';
const Telefonos = (props) =>{
    const numeros = props.numeros.map((n) =>
        <React.Fragment key={n.idNumero}>
            <span><i className={n.clase}></i></span>
            <p><a href={"tel:+503" + n.numero.replace("-", "")}>{n.numero}</a></p>
        </React.Fragment>
    );

    return(
        <div>
            {numeros}
        </div>
    );
}

export default Telefonos;