import React from 'react';
import Encabezado from '../../comunes/encabezado';
import '../../css/noticias.css';
import {Link} from 'react-router-dom';

const ContenidoNoticia = (props) =>{
    return(
        <article className="noticia">
            <div className="encabezado-noticia">
                <img src={props.noticia.imagen} alt={"Imagen noticia " + props.noticia.titulo} className="imagen-noticia"/>
            </div>
            <p className="fecha">Publicado: {props.noticia.fecha}</p>
            <div className="cuerpo-noticia">
                <h3 className="titulo-noticia">{props.noticia.titulo}</h3>
            </div>
        </article>
    );
}

const Noticia = (props) =>{
    if (!props.noticia){
        return null;
    }
    return(
        <Link to={"/noticias/" + props.noticia.idNoticia}>
            <ContenidoNoticia noticia={props.noticia}/>
        </Link>
    );
}

const SeccionNoticias = () =>{
    const noticias = [
        {idNoticia: 1, titulo: "Lorem ipsum dolor sit amet consectetur adipisicing elit.", imagen: "/img/img-fondo1.jpg", fecha: "16/10/2020"},
        {idNoticia: 2, titulo: "Lorem ipsum dolor sit amet consectetur adipisicing elit.", imagen: "/img/img-fondo2.jpg", fecha: "16/10/2020"},
        {idNoticia: 3, titulo: "Lorem ipsum dolor sit amet consectetur adipisicing elit.", imagen: "/img/img-fondo3.jpg", fecha: "16/10/2020"}
    ];

    return(
        <div className="seccion-noticias">
            <Noticia noticia={noticias[0]}/>
            <Noticia noticia={noticias[1]}/>
            <Noticia noticia={noticias[2]}/>
        </div>
    );
}

const UltimasNoticias = () =>{
    return(
        <section className="noticias">
            <div className="contenedor-noticias">
                <Encabezado titulo="ULTIMAS NOTICIAS" color="rgb(55, 110, 182)" idContenedor="titulo-ultimas-noticias" />
                <SeccionNoticias/>
            </div>
        </section>
    );
}

export {UltimasNoticias, Noticia};