import React, { useEffect, useState } from 'react';
import '../css/slider.css';

const ocultar = () =>{
    document.querySelector(".banner-texto").style.visibility = "hidden";
    document.querySelector(".banner-texto").style.opacity = 0;
    if (window.innerWidth > 915){
        document.querySelector(".banner-texto").style.left = "0%";
    }
    else{
        document.querySelector(".banner-texto").style.bottom = "0%";
    }
}

const mostrar = () =>{
    document.querySelector(".banner-texto").style.visibility = "visible";
    document.querySelector(".banner-texto").style.opacity = 1;
    if (window.innerWidth > 915){
        document.querySelector(".banner-texto").style.left = "5%";
    }
    else{
        document.querySelector(".banner-texto").style.bottom = "10%";
    }
}

const BannerTitulo = (props) =>{
    const contenido = props.contenido;
    useEffect(() => {
        ocultar();
        let animar = setTimeout(() =>{
            mostrar();
        }, 700);

        return () =>{
            clearTimeout(animar);
        }
    });

    return(
        <div className="banner-texto" style={{opacity: 0, left: "0%"}}>
            <span>{contenido}</span>
        </div>
    );
}

const Banner = (props) =>{
    const banner = props.slide;
    return(
        <React.Fragment key={banner.id}>
            <div className="banner" style={{backgroundImage: `url(${banner.imagen})`}}>
            </div>
            <BannerTitulo contenido={banner.contenido}/>
        </React.Fragment>
    );
}

const BannerContenedor = () =>{
    const [contador, setContador] = useState(0);
    const banner = [
        {id: 1, imagen: "img/banner-1.jpg", contenido: "Ejecutar y apoyar campañas relacionadas con la prevención de la violencia juvenil, campañas que fomenten una cultura de paz y aquellas relacionadas al cuido del medio ambiente."},
        {id: 2, imagen: "img/banner-2.jpg", contenido: "Desarrollar programas de voluntariado en las Instituciones Educativas y en las Comunidades."},
        {id: 3, imagen: "img/banner-3.jpg", contenido: "Generar espacios para la participación activa de la juventud a través de diversos medios de comunicación, incluyendo además debates, foros y seminarios."}
    ];

    useEffect(() =>{
        let tiempo = setInterval(()=>{
            cambiarSlide(true);
        }, 14000);

        return () =>{
            clearInterval(tiempo);
        }
    });

    const cambiarSlide = siguiente =>{
        if (siguiente){
            setContador((contador < banner.length - 1) ? (contador + 1) : 0);
        }
        else{
            setContador((contador > 0) ? (contador - 1) : (banner.length - 1));
        }
    }

    return(
        <section className="contenedor-banner">
            <Banner slide={banner[contador]}/>
            {
                banner.length > 1?
                <React.Fragment key={0}>
                    <div id="atras" onClick={() => cambiarSlide(false)} className="controles"><i className="fas fa-angle-left"></i></div>
                    <div id="siguiente" onClick={() =>cambiarSlide(true)} className="controles"><i className="fas fa-angle-right"></i></div>
                </React.Fragment>
                :
                null
            }
        </section>
    );
}

export default BannerContenedor;